import { ip } from '../networkPrefix'
import axios,{ get,post,patch,put } from 'axios'

async function exec(){

    await axios.get(`${ip}/`)

}

exec()

export default {

    authNegocio(negocio){

        return new Promise(async (resolve,reject) => {
            try {

            
                const data = (await axios.put(`${ip}/negocio/login`,{},{params:negocio})).data
        

                resolve(data)

            } catch (error) {

                reject(error)
                throw error
            }


        })    

    
    },
    novoNegocio(item){
        
        return new Promise(async (resolve,reject)=>{
            try {


            
                const data = (await post(`${ip}/negocio`,{},{params:item})).data

                resolve(data)
                
            } catch (error) {

                reject(error)

                throw error
            }


        })    

    },

    getNegocioById(){
        
        return new Promise(async (resolve,reject)=>{
            try {

            
                const data = (await get(`${ip}/empresa`)).data
        

                resolve(data)

            } catch (error) {

                reject(error)
                throw error
            }


        })    

    },
    
    updateNegocioById(item){
        
        
        return new Promise(async (resolve,reject)=>{
            try {

                const data = (await axios.patch(`${ip}/empresa`,{},{params:item})).data
                resolve(data)
                
            } catch (error) {

                reject(error)

                throw error
            }


        })    
    },
    
    apagarNegocioById(item){
        
        
        return new Promise(async (resolve,reject)=>{
            try {


                const data = (await axios.delete(`${ip}/empresa/apagar`,{ params: item })).data
                resolve(data)
  
                
            } catch (error) {

                reject(error)

                throw error
            }


        })    
    }




}